import * as React from 'react';
import { getImage, getSrc } from 'gatsby-plugin-image';
import { graphql, useStaticQuery } from 'gatsby';

const Seo = ({ title }) => {
  const data = useStaticQuery(graphql`
    query($id: String) {
      site {
        siteMetadata {
          title
          description
          image
          siteUrl
        }
      }
      mdx(id: {eq: $id}) {
        excerpt(pruneLength: 200)
        frontmatter {
          title
          tags
          date(formatString: "MMMM DD, YYYY")
          hero_image_alt
          hero_image_credit_link
          hero_image_credit_text
          hero_image {
            childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  }
`);

  const heroImage = getImage(data.mdx.frontmatter.hero_image);
  const heroImageUrl = getSrc(heroImage);
  const blurb = data.mdx.excerpt || data.site.siteMetadata.description;

  return (
    <>
      <html lang="en" />
      <meta name="description" content={blurb} />
      <meta name="image" content={data.site.siteMetadata.image} />
      <meta property="og:image" content={`${data.site.siteMetadata.siteUrl}${heroImageUrl}`} />
      <title>{title.replace("/", "")} ⊛ {data.site.siteMetadata.title}</title>
    </>
  );
};

export default Seo;